/* Links */
a,
a:focus,
a:hover {
  color: $link-color;
}

/* Custom default button */
.btn-secondary,
.btn-secondary:hover,
.btn-secondary:focus {
  color: $button-color;
  text-shadow: none;
  /* background-color: #fff; */
  border: .05rem solid $white;
}


/*
 * Base structure
 */

html,
body {
  height: 100%;
  background-color: $gray-darkest;
}

body {
  display: -ms-flexbox;
  display: flex;
  color: $white;
  text-shadow: 0 .05rem .1rem rgba(0, 0, 0, .5);
  box-shadow: inset 0 0 5rem rgba(0, 0, 0, .5);
}
